*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 4fr;
  min-height: 100vh;
}

/* Sidebar */
.sidebar {
  background: #e6e5e1;
  transition: width 0.3s ease;
}

.sidebar.collapsed {
  width: 100px;
}

.sidebar-header {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
}

.sidebar-header button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: large;
}

.dropdown {
  border-top: #cfcfcf solid 1px;
  margin: 10px 0;
}

.dropdown-header {
  padding: 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.dropdown-header i {
  margin-right: 10px;
}

.dropdown-content {
  padding-left: 20px;
}

.dropdown.inactive .dropdown-content {  
  display: none;
}

.menu-item {
  padding: 8px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.menu-item i {
  margin-right: 10px;
}

.menu-item:hover {
  background: #f5f5f5;
}

.sidebar-footer {
  padding: 1rem;
  border-top: 1px solid #ddd;
  margin-top: auto;
}

.add-todo-btn {
  width: 100%;
  padding: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
  border: none;
  background: #c9c9c9;
  border-radius: 4px;
  cursor: pointer;
}

.add-todo-btn:hover {
  color: white;
  background: #797979;
}

.sidebar.collapsed .sidebar-content,
.sidebar.collapsed .sidebar-footer {
  display: none;
}

/* Main content */
#main {
  padding: 20px;
  box-sizing: border-box;
  background-color: #f5f5f5;
}

header {
  display: flex;
  align-items: center;
  padding: 10px;
  border-radius: 4px;
}

header h1 {
  margin: 0;
  margin-left: 10px;
}

.title-icon {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  background-color: white;
  display: flex;
  align-items: center;
  padding: 8px;
  margin: 8px 0;
  transition: background-color 0.3s, text-decoration 0.3s;
  cursor: pointer;
}

li:hover {
  background-color: #d0e3e4;
}

li.completed {
  background-color: #f5f5f5;
  color: #a1a1a1;
  text-decoration: line-through;
}

li.completed span.due-date {
  background-color: #dddddd;
  color: #a1a1a1;
  text-decoration: line-through;
}

li span.due-date {
  background-color: #929292;
  color: white;
  padding: 2px 4px;
  margin-right: 8px;
  border-radius: 4px;
}

li input[type="checkbox"] {
  margin-right: 8px;
}

.priority {
  padding: 2px 4px;
  margin-left: 8px;
  border-radius: 4px;
  color: white;
}

.priority.high {
  background-color: #ff6666;
}

.priority.medium {
  background-color: #ffcc66;
}

.priority.low {
  background-color: #66cc66;
}

/* Todo Modal */
.modal {
    display: none;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.4);
}

.modal-content {
    background-color: #fefefe;
    margin: 15% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
    max-width: 500px;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    cursor: pointer;
}

.close:hover {
    color: black;
}

.todo-details p {
    margin: 10px 0;
}

.modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.modal-buttons {
    display: flex;
    gap: 10px;
    align-items: center;
}

.edit-btn, .save-btn {
    padding: 5px 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.edit-btn {
    background: #4CAF50;
    color: white;
}

.save-btn {
    background: #2196F3;
    color: white;
}

.delete-btn {
  background: #dc3545;
  color: white;
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.delete-btn:hover {
  background: #c82333;
}

[contenteditable="true"] {
    border-bottom: 1px dashed #999;
    padding: 2px;
}

.todo-details select, .todo-details input {
    margin-left: 5px;
    padding: 2px;
}


/* Media queries */
@media screen and (max-width: 1024px) {
  body {
    grid-template-columns: 25% 75%;
  }
}

@media screen and (max-width: 768px) {
  body {
    grid-template-columns: 30% 70%;
  }
}

@media screen and (max-width: 480px) {
  body {
    grid-template-columns: 100%;
  }
  
  .sidebar {
    position: fixed;
    z-index: 100;
    transform: translateX(-100%);
  }
}

/* To be done */
.add-category {
  display: none;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,cAAc;AACd;EACE,SAAS;EACT,UAAU;EACV,sBAAsB;AACxB;;AAEA;EACE,SAAS;EACT,UAAU;EACV,aAAa;EACb,8BAA8B;EAC9B,iBAAiB;AACnB;;AAEA,YAAY;AACZ;EACE,mBAAmB;EACnB,2BAA2B;AAC7B;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,aAAa;EACb,aAAa;EACb,8BAA8B;AAChC;;AAEA;EACE,gBAAgB;EAChB,YAAY;EACZ,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,6BAA6B;EAC7B,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,eAAe;EACf,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,eAAe;AACjB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,0BAA0B;EAC1B,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,aAAa;EACb,aAAa;EACb,mBAAmB;EACnB,SAAS;EACT,YAAY;EACZ,mBAAmB;EACnB,kBAAkB;EAClB,eAAe;AACjB;;AAEA;EACE,YAAY;EACZ,mBAAmB;AACrB;;AAEA;;EAEE,aAAa;AACf;;AAEA,iBAAiB;AACjB;EACE,aAAa;EACb,sBAAsB;EACtB,yBAAyB;AAC3B;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,aAAa;EACb,kBAAkB;AACpB;;AAEA;EACE,SAAS;EACT,iBAAiB;AACnB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,qBAAqB;EACrB,UAAU;AACZ;;AAEA;EACE,uBAAuB;EACvB,aAAa;EACb,mBAAmB;EACnB,YAAY;EACZ,aAAa;EACb,uDAAuD;EACvD,eAAe;AACjB;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;EACzB,cAAc;EACd,6BAA6B;AAC/B;;AAEA;EACE,yBAAyB;EACzB,cAAc;EACd,6BAA6B;AAC/B;;AAEA;EACE,yBAAyB;EACzB,YAAY;EACZ,gBAAgB;EAChB,iBAAiB;EACjB,kBAAkB;AACpB;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,gBAAgB;EAChB,gBAAgB;EAChB,kBAAkB;EAClB,YAAY;AACd;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;AAC3B;;AAEA,eAAe;AACf;IACI,aAAa;IACb,eAAe;IACf,aAAa;IACb,OAAO;IACP,MAAM;IACN,WAAW;IACX,YAAY;IACZ,iCAAiC;AACrC;;AAEA;IACI,yBAAyB;IACzB,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,UAAU;IACV,gBAAgB;IAChB,kBAAkB;IAClB,qCAAqC;AACzC;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,eAAe;IACf,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,kBAAkB;IAClB,eAAe;AACnB;;